html,
body {
  min-height: 100vh;
  margin: 0;
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 6px;
  height: 0px;
}

::-webkit-scrollbar-thumb {
  background-color: darkgrey;
}

.page-body-wrapper {
  padding-top: 87px;
}

.heading_1 {
  font-size: 62px;
}

.heading_3 {
  font-size: 40px;
}

.heading_4 {
  font-size: 32px;
}

.heading_5 {
  font-size: 20px;
}

.small_text {
  font-size: 14px;
}

.medium_text {
  font-size: 16px;
}

.container-fluid {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.row {
  width: 100%;
  margin: 0 !important;
}

.page-element {
  padding: 0 75px;
  font-size: 14px;
}

.bg-primary {
  background: #E1DDD6;
}

.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.img-container {
  position: relative;
  text-align: center;
  width: 100%;
}
.img-container img {
  width: 100%;
  height: auto;
}
.img-container .img-text {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

[class*=col-] {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.back-to-top {
  position: fixed;
  right: -10px;
  bottom: 50%;
  cursor: pointer;
  color: #959595;
  font-size: 58px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-size: 16px;
  color: #333333;
}

.get-in-touch {
  position: fixed;
  left: -10px;
  bottom: 50%;
  cursor: pointer;
  color: #959595;
  font-size: 58px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-size: 16px;
  color: #333333;
}

.get-in-touch:hover {
  color: #333333;
  text-decoration: none;
}

.boder-none {
  border: none !important;
}

.content-center {
  justify-content: center;
}
